<template>
  <div id="img-del">
    <img src="../../assets/img/slide1.jpg" alt="">
    <a
      href="javascript:;"
      class="del-img"
      @click="del">删除图片</a>
  </div>
</template>

<script>
  export default {
    name: 'imgDel',
    data () {
      return {

      }
    },
    methods: {
      del(){
        this.$emit('delimg')
      }
    },
    computed: {

    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #img-del{
    display: inline-block;
    width: 200px;
    height: 160px;
    position: relative;
  }
  img{
    float: left;
    box-sizing: border-box;
    width: 200px;
    height: 130px;
    border: 2px solid #00ca79;
  }
  .del-img{
    float: left;
    display: inline-block;
    box-sizing: border-box;
    width: 100%;
    height: 30px;
    line-height: 30px;
    font-weight: bold;
    background-color: #00ca79;
    color: #fff;
    text-align: center;
  }
</style>



