<template>
  <div id="wrap">
    <section-title sec-title="首页轮播图"></section-title>
    <div class="content">
      <img-del
        class="img-list"
        @delimg="showDialog"
        ></img-del>
      <img-add class="img-list"></img-add>
    </div>
    <g-dialog
      class="g-dialog"
      :class="{ 'display-block': isAlertShow }"
      @hidedialog="hideDialog"></g-dialog>
  </div>
</template>

<script>
  import sectionTitle from '../others/section-title'
  import imgDel from './img-del'
  import imgAdd from './img-add'
  import gDialog from '../others/dialog'
  export default {
    name: 'img',
    components: {
      sectionTitle,
      imgDel,
      imgAdd,
      gDialog
    },
    data () {
      return {
        isAlertShow: false
      }
    },
    methods: {
      showDialog(){
        this.isAlertShow = true
      },
      hideDialog(){
        this.isAlertShow = false
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #wrap{
    position: relative;
    background-color: #fff;
    width: 1030px;
    height: 968px;
    margin: 22px 0 0 26px;
  }
  .content{
    margin-top: 40px;
    padding: 0 50px;
  }
  .img-list{
    margin-left: 24px;
    margin-top: 10px;
  }
  .confirm{
    width: 220px;
    height: 120px;
    background-color: #00ca79;
    border-radius: 6px;
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%);
    margin-top: -300px;
  }
  .confirm p{
    line-height: 66px;
    text-align: center;
    color: #fff;
  }
  .confirm a{
    padding: 2px 4px;
    /*background-color: #fff;*/
    border-radius: 2px;
    color: #fff;
    font-weight: bold;
    transition: .3s;
  }
  .confirm a:hover{
    background-color: #fff;
    color: #000;
  }
  .g-dialog{
    display: none;
  }
</style>



