<template>
  <div id="g-dialog-wrap">
    <div id="g-dialog">
      <div class="g-dialog-header">
        是否删除图片？
        <span
          class="g-dialog-close"
          @click="close">x</span>
      </div>
      <div class="g-dialog-btns">
        <a href="javascript:;" @click="cancel">取消</a>
        <a href="javascript:;" @click="confirm">确认</a>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'gDialog',
    methods: {
      cancel(){
        this.$emit('hidedialog')
      },
      confirm(){
          console.log('confirm')
      },
      close(){
        this.$emit('hidedialog')
      }
    }
  }
</script>

<style scoped>
  #g-dialog-wrap{
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .5);
  }
  #g-dialog{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    position: relative;
    width: 260px;
    height: 140px;
    background-color: #fff;
    border: 1px solid #00ca79;
    box-sizing: border-box;
  }
  #g-dialog .g-dialog-header{
    background-color: #00ca79;
    line-height: 30px;
    text-indent: 10px;
    color: #fff;
    font-weight: bold;
    transition: .5s;
  }
  #g-dialog .g-dialog-btns{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    margin-top: 30px;
  }
  #g-dialog .g-dialog-btns a{
    padding: 4px 6px;
    font-size: 12px;
    box-sizing: border-box;
    border-radius: 4px;
  }
  #g-dialog .g-dialog-btns a:first-child{
    border: 1px solid #999;
    margin-right: 6px;
  }
  #g-dialog .g-dialog-btns a:first-child:hover{
    color: #00ca79;
    border-color: #00ca79;
  }
  #g-dialog .g-dialog-btns a:last-child{
    color: #fff;
    background-color: #00ca79;
    margin-left: 6px;
  }
  .g-dialog-close{
    float: right;
    margin-right: 10px;
    cursor: pointer;
  }
</style>



